import React from 'react';
import {Link} from 'react-router-dom';

import auth from '../../backendAPI/auth';


export default function WelcomePage(props) {
  // const backgroundStyle = {
  //   backgroundImage: 'url("./img/grid.png")',
  //   backgroundRepeat: 'repeat-x',
  //   backgroundAttachment: 'fixed'
  // }
  function loginDemoUser() {
    const cb = ()=>{ props.history.push('/company/overview/') };
    auth.login('demo', 'DemoUserPassword', cb);
  };

  return (
    <React.Fragment>
      <div className='desk-bg-color-primary text-dark content-height'>
      <div className='card' style={{background:'none', border:'none'}}>
        <div className='container card border-0 my-4 px-3 pt-3 pb-4 w-100' style={{background:'rgba(5,10,40, 0.3)'}}>
          <div className="card p-3" style={{background:'rgba(255,255,255, 0.9)'}}>
            <h3 className='text-center'>   <b>CI Cloud</b></h3>
            <h5 className='text-center text-secondary'>  
              <Link className='px-2' to="/user/login/"></Link>
              
              <Link className='px-2' to="/user/registration/"></Link>
            </h5>
          </div>

          <div className="row equal px-1 py-5">
            <CardContainer>
              <h6>      IoT </h6>
            </CardContainer>

            <CardContainer>
              <h6>         </h6>
            </CardContainer>

            <CardContainer>
              <h6>     </h6>
            </CardContainer>

            <CardContainer>
              <h6> API       IoT  </h6>
            </CardContainer>

            <CardContainer>
              <h6>        </h6>
            </CardContainer>

            <CardContainer>
              <h6>     
                <button type="button" className="btn btn-link px-2 py-0 m-0" onClick={loginDemoUser}>
                  -
                </button>  
              </h6>
            </CardContainer>
          </div>  

          
          <UpdateDescriptionContainer>
            <h5 className='pb-3 text-dark text-center'>  : </h5>
            <UpdateDescriptionTitle>  0.1.7 </UpdateDescriptionTitle>
            <UpdateDescriptionHeader>     </UpdateDescriptionHeader>
            <UpdateDescriptionMessage> 
                     , 
                    .
                       .
            </UpdateDescriptionMessage> 
            <UpdateDescriptionHeader>       </UpdateDescriptionHeader>
            <UpdateDescriptionMessage> 
                  
               ,       . 
                         .
            </UpdateDescriptionMessage>  

            <UpdateDescriptionTitle>  0.1.6 </UpdateDescriptionTitle>
            <UpdateDescriptionHeader>    </UpdateDescriptionHeader>
            <UpdateDescriptionMessage> 
                  
               ,        , 
                     .
            </UpdateDescriptionMessage>  
          </UpdateDescriptionContainer>
        </div>
      </div>
      </div>
    </React.Fragment>
  );
}


function CardContainer (props) {
  return (
    <div className="col-4 p-2">
      <div className="card h-100 border-0" style={{background:'rgba(255,255,255, 0.9)'}}>
        <div className="card-body text-center text-dark">
          {props.children}
        </div>
      </div>
    </div>
  )
}


function UpdateDescriptionContainer (props) {
  return (
    <div className="card border-0 mb-3" style={{background:'rgba(255,255,255, 0.9)'}}>
    <div className="card-body">
      {props.children}
    </div>
    </div>
  )
}

function UpdateDescriptionTitle (props) {
  return <h6 className='text-primary border-top border-info pt-3'> {props.children} </h6> 
}

function UpdateDescriptionHeader (props) {
  return <h6 className='text-success'> {props.children} </h6> 
}

function UpdateDescriptionMessage (props) {
  return <p className='text-dark'> {props.children} </p> 
}